<template>
  <div id="app">
    <header>
      <div class="header-top-area">
        <div class="container">
          <div class="row justify-content-center">
            <div class="col-xl-12 col-lg-12 col-md-12">
              <div class="top-wallet-address text-center">
                <h4>Wallet Name: {{address}}</h4>
                <h4>Balance: {{balance}} ETH</h4>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="menu-wrap">
        <div class="container">
          <div class="row">
            <div class="col-xl-12">
              <div class="main-menu">
                <nav id="mobile-menu" style="display: block">
                  <ul>
                    <router-link to="/">Admin</router-link>|
                    <router-link to="/bidgame">User</router-link>
                  </ul>
                </nav>
              </div>
              <div class="mobile-menu"></div>
            </div>
          </div>
        </div>
      </div>
    </header>
    <router-view />
  </div>
</template>

<script>
import web3 from "./contract/web3";
import * as utils from "./utils";

export default {
  name: "Home",
  components: {},
  data: function () {
    return {
      balance: -1,
      address: "",
    };
  },
  created: async function () {
    const accounts = await web3.eth.getAccounts();
    this.address = utils.shortenAddress(accounts[0]);
    this.balance = utils.sunToEth(await web3.eth.getBalance(accounts[0]));

    web3.eth.subscribe("newBlockHeaders", async (err) => {
      if (err) {
        console.log(err);
      } else {
        this.balance = utils.sunToEth(await web3.eth.getBalance(accounts[0]));
      }
    });
  },
  destroyed: function () {},
};
</script>


