<template>
  <div class="container">
    <div class="row">
      <p>Owner: {{owernerAddress}}</p>
    </div>
    <div class="row">
      <p>Game Time: {{gameSettings.gameTimerSeconds}} seconds</p>
    </div>
    <div class="row">
      <p>% to Owner: {{gameSettings.comOwner/100}}%</p>
    </div>
    <div class="row">
      <p>% keep next round: {{gameSettings.keepPot/100}}%</p>
    </div>
    <div class="row">
      <p>% next bid: {{gameSettings.stepBid/100}}%</p>
    </div>
    <div class="row">
      <input placeholder="game time" v-model.number="gameTimerSeconds" type="number" />
      <button v-if="!loadingGameTime" @click="setGameTime">Edit</button>
      <div class="load-1" v-if="loadingGameTime">
        <div class="line"></div>
        <div class="line"></div>
        <div class="line"></div>
      </div>
    </div>
    <br />
    <div class="row">
      <input placeholder="% owner" v-model.number="comOwner" type="number" min="0" max="100" />
      <button v-if="!loadingComOwner" @click="setComOwner">Edit</button>
      <div class="load-1" v-if="loadingComOwner">
        <div class="line"></div>
        <div class="line"></div>
        <div class="line"></div>
      </div>
    </div>
    <br />
    <div class="row">
      <input
        placeholder="% keep next round"
        v-model.number="keepPot"
        type="number"
        min="0"
        max="100"
      />
      <button v-if="!loadingKeepPot" @click="setKeepPot">Edit</button>
      <div class="load-1" v-if="loadingKeepPot">
        <div class="line"></div>
        <div class="line"></div>
        <div class="line"></div>
      </div>
    </div>
    <div class="row">
      <input
        placeholder="% next bid"
        v-model.number="stepBid"
        type="number"
      />
      <button v-if="!loadingStepBid" @click="setStepBid">Edit</button>
      <div class="load-1" v-if="loadingStepBid">
        <div class="line"></div>
        <div class="line"></div>
        <div class="line"></div>
      </div>
    </div>
  </div>
</template>

<script>
import bidContract from "../contract/bidContractInstance";
// import * as utils from "../utils";
import Swal from "sweetalert2";
import web3 from "../contract/web3";

export default {
  name: "Admin",
  components: {},
  data: function () {
    return {
      owernerAddress: "",
      gameSettings: {},
      gameTimerSeconds: 0,
      comOwner: 0,
      keepPot: 0,
      stepBid: 0,
      loadingGameTime: false,
      loadingComOwner: false,
      loadingKeepPot: false,
      loadingStepBid: false
    };
  },
  created: async function () {
    this.init();
  },
  methods: {
    async setGameTime() {
      this.loadingGameTime = true;
      const accounts = await web3.eth.getAccounts();
      bidContract.methods
        .setGameTimerSeconds(this.gameTimerSeconds)
        .send({ from: accounts[0] })
        .on("transactionHash", () => {})
        .on("receipt", (receipt) => {
          if (receipt.status) {
            Swal.fire(
              "Information",
              `Edited game time to ${this.gameTimerSeconds} seconds`,
              "success"
            );
            this.init();
          } else {
            Swal.fire("Information", "Transaction failed", "error");
          }
          this.loadingGameTime = false;
        })
        .on("error", () => {
          Swal.fire("Information", "Transaction failed", "error");
          this.loadingGameTime = false;
        });
    },
    async setComOwner() {
      this.loadingComOwner = true;
      const accounts = await web3.eth.getAccounts();
      bidContract.methods
        .setComOwner(this.comOwner * 100)
        .send({ from: accounts[0] })
        .on("transactionHash", () => {})
        .on("receipt", (receipt) => {
          if (receipt.status) {
            Swal.fire(
              "Information",
              `Edited % owner to ${this.comOwner}%`,
              "success"
            );
            this.init();
          } else {
            Swal.fire("Information", "Transaction failed", "error");
          }
          this.loadingComOwner = false;
        })
        .on("error", () => {
          Swal.fire("Information", "Transaction failed", "error");
          this.loadingComOwner = false;
        });
    },
    async setStepBid() {
      this.loadingStepBid = true;
      const accounts = await web3.eth.getAccounts();
      bidContract.methods
        .setStepBid(this.stepBid * 100)
        .send({ from: accounts[0] })
        .on("transactionHash", () => {})
        .on("receipt", (receipt) => {
          if (receipt.status) {
            Swal.fire(
              "Information",
              `Edited % next bid to ${this.stepBid}%`,
              "success"
            );
            this.init();
          } else {
            Swal.fire("Information", "Transaction failed", "error");
          }
          this.loadingStepBid = false;
        })
        .on("error", () => {
          Swal.fire("Information", "Transaction failed", "error");
          this.loadingStepBid = false;
        });
    },
     async setKeepPot() {
      this.loadingKeepPot = true;
      const accounts = await web3.eth.getAccounts();
      bidContract.methods
        .setKeepPot(this.keepPot * 100)
        .send({ from: accounts[0] })
        .on("transactionHash", () => {})
        .on("receipt", (receipt) => {
          if (receipt.status) {
            Swal.fire(
              "Information",
              `Edited % keep next round to ${this.keepPot}%`,
              "success"
            );
            this.init();
          } else {
            Swal.fire("Information", "Transaction failed", "error");
          }
          this.loadingKeepPot = false;
        })
        .on("error", () => {
          Swal.fire("Information", "Transaction failed", "error");
          this.loadingKeepPot = false;
        });
    },
    async init() {
      this.owernerAddress = await bidContract.methods.getOwner().call();
      this.gameSettings = await bidContract.methods.gameSettings().call();
      this.gameTimerSeconds = this.gameSettings.gameTimerSeconds;
      this.comOwner = this.gameSettings.comOwner/100;
      this.keepPot = this.gameSettings.keepPot/100;
      this.stepBid = this.gameSettings.stepBid/100;
    },
  },
};
</script>

<style scoped lang="scss">
.line {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 15px;
  background-color: #4b9cdb;
}

.load-1 .line:nth-last-child(1) {
  animation: loadingA 1.5s 1s infinite;
}
.load-1 .line:nth-last-child(2) {
  animation: loadingA 1.5s 0.5s infinite;
}
.load-1 .line:nth-last-child(3) {
  animation: loadingA 1.5s 0s infinite;
}

@keyframes loadingA {
  0% {
    height: 15px;
  }
  50% {
    height: 35px;
  }
  100% {
    height: 15px;
  }
}
</style>